@font-face {
  font-family: "Montserrat";
  src: url(./Montserrat-SemiBold.ttf);
}


.App {
  width: 60%;
  margin: 0 auto;
  font-family: "Montserrat";
  color: #222;
  border: 1px solid #aaa;
  min-height: 200px;
  border-radius: 5px;
}

.App > h1 {
  text-align: center;
  text-transform: uppercase;
  font-size: 2.5rem;
}

.TodoForm {
  width: 60%;
  margin: 0 auto;
}
.TodoForm > form input {
  width: 100%;
  height: 40px;
  border: 2px solid #4390fe;
  border-radius: 2px;
  padding-left: 10px;
  box-sizing: border-box;
  font-family: "Karla";
  font-size: 16px;
}

.TodoList {
  list-style-type: none;
  width: 60%;
  margin: 20px auto;
  border: 1px solid #aaa;
  border-radius: 5px;
  padding: 0;
}

.TodoList .list-item {
  display: block;
  padding: 10px 20px;
  border-bottom: 1px solid #aaa;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.TodoList .list-item span {
  cursor: pointer;
}

.TodoList .list-item span.completed {
  text-decoration: line-through;
}

.TodoList .list-item .imgs {
  display: flex;
}

.TodoList .list-item img{
  cursor: pointer;
  height: 20px;
  margin-left: 5px;
  transition: 100ms;
}

.TodoList .list-item .delete-img {
  margin-left: 15px;
}

.TodoList .list-item form {
  width: 70%;
}

.TodoList .list-item input {
  padding: 0 10px;
  width: 100%;
  font-size: 15px;
  width: 100%;
}

.hidden {
  display: none;
}

@media (max-width: 1200px) {
  .TodoList, .TodoForm{
    width: 90%;
  }
}

@media (max-width: 1000px) {
  .App {
    width: 70%;
  }
}

@media (max-width: 700px) {
  .App {
    width: 90%;
  }
}
